import React, { useState } from 'react';
import { DataTable, DataTableColumn } from '@salesforce/design-system-react';

const Table = ({ items }) => {

    const [selection, setSelection] = useState([]);

    function onRowChange(event, data) {
        setSelection(data.selection);
        console.log(event, data)
    }

    const columns = [

        <DataTableColumn
            key="xml-name"
            label="XML NAME"
            property="XMLName"
            fixedLayout
        />,

        <DataTableColumn
            key="dir-name"
            label="DIRECTORY NAME"
            property="DirName"
            fixedLayout
        />,

        <DataTableColumn
            key="suffix"
            label="SUFFIX"
            property="Suffix"
            fixedLayout
        />,

        <DataTableColumn
            key="HasMetaFile"
            label="HAS META FILE?"
            property="HasMetaFile"
            fixedLayout
        />,

        // <DataTableColumn
        //     key="child-objects"
        //     label="Child Objects"
        //     property="ChildObjects" />,
    ];

    return (
        <div className="dataTable" style={{ overflow: 'auto' }}>
            <DataTable
                items={items}
                id="datatable"
                fixedHeader
                fixedLayout
                joined
                // stackedHorizontal
                // selectRows="checkbox"
                onRowChange={onRowChange}
                selection={selection}
            >
                {columns}
            </DataTable>
        </div>
    );

}

export default Table;