import React, { useState, useEffect, useRef } from 'react';
import Header from './components/Header';
import DataTable from './components/Table';
import Loading from './components/Loading';
import './App.css';

const App = () => {

  const [data, setData] = useState([]);
  const [items, setItems] = useState([]);
  const [count, setCount] = useState(0);
  const [isLoading, setIsLoading] = useState(true);
  
  useEffect(() => {
    const json = require('../src/assets/data.json');
    setData(json);
    setItems(json);
    setCount(json.length);
    setIsLoading(false);
  }, []);
  
  const updateItems = (search) => {
    setIsLoading(true);
    const items = data.filter((item) => {
      return item.XMLName.toLowerCase().includes(search);
    });
    setItems(items);
    setCount(items.length);
    setIsLoading(false);
  }

  const find = (text) => {
    updateItems(text);
  }

  return (
    <div className="app">
      {isLoading && <Loading />}
      <Header search={find} count={count}/>
      <DataTable items={items} />
      <div className="copy">
        <p>Developer by: <a href="https://www.linkedin.com/in/willyanmarques/" target="_blank">Willyan Marques</a></p>
      </div>
    </div>
  );

}

export default App;
