import React from 'react';
import { IconSettings, Icon, PageHeader, PageHeaderControl, Input, InputIcon } from '@salesforce/design-system-react';

const Header = ({search, count}) => {

    function handleOnChange(e){
        const text = e.target.value.toLowerCase();
        search(text);
    }

    const actions = () => (
        <PageHeaderControl>
            <Input
                iconRight={
                    <InputIcon
                        assistiveText={{
                            icon: 'Search',
                        }}
                        name="search"
                        category="utility"
                    />
                }
                id="search"
                placeholder="search for xml name column"
                onKeyUp={handleOnChange}
            />
        </PageHeaderControl>
    );

    return (
        <IconSettings iconPath="./icons" >
            <div className="pageHeader">
                <PageHeader
                    icon={
                        <Icon
                            assistiveText={{
                                label: 'Home',
                            }}
                            category="standard"
                            name="apex"
                            style={{
                                backgroundColor: '#215ca0',
                                fill: '#ffffff',
                            }}
                            title="Home"
                        />
                    }
                    joined
                    onRenderActions={actions}
                    label={`${count} items`}
                    title="Salesforce Migration"
                    truncate
                    variant="object-home"
                />
            </div>
        </IconSettings>
    );

}

export default Header;